.card {
  width: 120px;
  height: 120px;
  border-radius: var(--main-radius);
  position: relative;
  transform-style: preserve-3d;
  transition: transform var(--duration);
  cursor: pointer;
  user-select: none;
}
.card.flip {
  transform: rotateY(180deg);
}
card.right {
  pointer-events: none;
}

.front, .back {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  backface-visibility: hidden;
  border-radius: var(--main-radius);
}
.front {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.front img {
  max-width: 75%;
  display: block;
  backface-visibility: hidden;
  pointer-events: none;
}

.back {
  transform:rotateY(180deg);
}