.lead-board {
}

.heading {
  font-size: 1.5em;
  font-weight: bold;
  font-style: italic;
  background-color: var(--clr-blue);
  padding: .3em 1em;
  border-radius: var(--main-radius);
}

.leaders {
  margin-top: 1em;
}

.leader {
  display: flex;
  background-color: var(--clr-yellow);
  font-weight: bold;
  padding: .2em .8em;
  border-radius: var(--main-radius);
  font-style: italic;
  text-transform: capitalize;
  justify-content: space-between;
}
.leader {
  margin-bottom: .7rem;
}
.leader:last-child {
  margin-bottom: 3rem;
}
