header {
  display: flex;
  padding: 70px 0;
  justify-content: space-between;
  align-items: center;
  width: 770px;
  margin: auto;
}
h1 {
  font-size: 3em;
  font-weight: bold;
  font-style: italic;
}
.wrong-tries {
  font-size: 2.5em;
  padding: 0.3rem 1rem;
  font-weight: bold;
  border-radius: var(--main-radius);
}

@media (max-width: 767px) {
  header {
    width: 80%;
  }
  h1{
    font-size: 2em;
  }
}